/* Mobile Menu toggle/hamburger */
.navbar-toggler {
  padding: 0;
  background: none;
  margin: 0;
  border: none;
  border-radius: none;
  font-size: 1rem;
  box-shadow: none;
  text-shadow: none;
  padding: 15px 10px;
  border: 1px solid transparent;
}
@media (min-width: 400px) {
  .navbar-toggler {
    top: 10px;
  }
}
@media (min-width: 1200px) {
  .navbar-toggler {
    display: none;
  }
}
.navbar-toggler .hamburger-bars,
.navbar-toggler .hamburger-bars:after,
.navbar-toggler .hamburger-bars:before {
  display: block;
  width: 24px;
  height: 3px;
  border-radius: 10px;
  background: #005f86;
}
.navbar-toggler .hamburger-bars {
  position: relative;
}
.navbar-toggler .hamburger-bars:after,
.navbar-toggler .hamburger-bars:before {
  content: "";
  position: absolute;
}
.navbar-toggler .hamburger-bars:before {
  top: -6px;
}
.navbar-toggler .hamburger-bars:after {
  top: 6px;
}

.navbar .navbar-toggler {
  top: 40px;
  background: #fff;
  box-shadow: 1px 2px 3px #888;
  -webkit-box-shadow: 1px 2px 3px #888;
  -moz-box-shadow: 1px 2px 3px #888;
  border-radius: 3px;
  padding: 15px 10px;
}

.navbar .navbar-toggler:focus {
  background: #fff;
}

.navbar .navbar-toggler .hamburger-bars,
.navbar .navbar-toggler .hamburger-bars:after,
.navbar .navbar-toggler .hamburger-bars:before {
  background: #333f48;
}

.navbar-toggler .hamburger-bars {
  transform: rotate(0deg);
  transition: transform 0.275s ease-out;
}
.navbar-toggler .hamburger-bars:after, .navbar-toggler .hamburger-bars:before {
  transition: top 0.15s 0.275s ease-out, transform 0.275s ease-out;
}

.navbar-toggler.is-open .hamburger-bars {
  transform: rotate(-45deg);
  transition: transform 0.275s 0.15s ease-out;
}
.navbar-toggler.is-open .hamburger-bars:after, .navbar-toggler.is-open .hamburger-bars:before {
  top: 0;
  transform: rotate(-90deg);
  transition: top 0.15s ease-out, transform 0.275s 0.15s ease-out;
}

