.ann__landing_circle {
  fill: none;
  transform-origin: center;
}
.ann__landing_half_circle_blue {
  stroke: #3c4952;
  stroke-width: 10;
}
.ann__landing_half_circle_orange {
  stroke: #be5701;
  stroke-width: 7;
  transform: rotate(50deg);
}
.ann__landing_half_circle_yellow {
  stroke: #d9ad76;
  stroke-width: 5;
  transform: rotate(40deg);
}
.ann__landing_half_circle_dashed {
  fill: none;
  stroke: #3c4952;
  stroke-width: 0.5;
  stroke-dasharray: 2, 2; /* 10 units dash, 10 units gap */
  transform: rotate(35deg);
}
.ann__landing_rotate_counter_90 {
  transform-origin: center;
  animation: rotateCounter90 1s forwards ease 0s;
}
.ann__landing_rotate_counter_95 {
  transform-origin: center;
  animation: rotateCounter95 1s forwards ease 0.25s;
}
.ann__landing_rotate_85 {
  transform-origin: center;
  animation: rotate85 1s forwards ease 0.5s;
}
.ann__landing_rotate_81 {
  transform-origin: center;
  animation: rotate81 1s forwards ease 0.75s;
}
.ann__landing_fade_in {
  animation: fadeIn 1s ease forwards;
}
.ann__landing_fade_in_delay_1 {
  animation-delay: 1s;
}
.ann__landing_fade_in_delay_2 {
  animation-delay: 1.25s;
}
.ann__landing_circle_cme, .ann__landing_circle_2023 {
  opacity: 0;
}
.ann__landing_fade_in_and_up_delay_1 {
  opacity: 0;
  animation: fadeIn 1s ease forwards, slideIn 1s ease forwards;
  animation-delay: 0.5s;
}
.ann__landing_fade_in_and_up_delay_2 {
  opacity: 0;
  animation: fadeIn 1s ease forwards, slideIn 1s ease forwards;
  animation-delay: 0.7s;
}
.ann__landing_fade_in_and_up_delay_3 {
  opacity: 0;
  animation: fadeIn 1s ease forwards, slideIn 1s ease forwards;
  animation-delay: 0.9s;
}
@keyframes fadeInBackground {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes rotateCounter90 {
  from {
    transform: rotate(45deg);
  }
  to {
    transform: rotate(-90deg);
  }
}
@keyframes rotateCounter95 {
  from {
    transform: rotate(50deg);
  }
  to {
    transform: rotate(-110deg);
  }
}
@keyframes rotate85 {
  from {
    transform: rotate(40deg);
  }
  to {
    transform: rotate(200deg);
  }
}
@keyframes rotate81 {
  from {
    transform: rotate(35deg);
  }
  to {
    transform: rotate(200deg);
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes slideIn {
  from {
    transform: translateY(24px);
  }
  to {
    transform: translateY(0);
  }
}

