/* Lightbox container */
.lightbox {
  opacity: 0; /* Initially set opacity to 0 */
  visibility: hidden; /* Initially set visibility to hidden */
  position: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  max-height: 100vh;
  background-color: rgba(0, 0, 0, 0.84);
  z-index: 9999; /* Increase the z-index value */
  overflow: auto;
  transition: opacity 0.3s ease, visibility 0s linear 0.3s; /* Add a transition for opacity */
}

/* Make the lightbox visible when it's displayed */
.lightbox.lightbox-open {
  opacity: 1;
  visibility: visible; /* Make the element visible */
}

/* Lightbox image */
#lightbox-image {
  max-width: 90%;
  max-height: 80%;
  margin: 60px auto 10px auto;
  display: block;
  border: solid #fff 20px;
}

#image-lightbox figcaption {
  max-width: 70%;
  color: #fff;
}
#image-lightbox figcaption * {
  color: #fff;
}

/* Close button */
.close-button {
  position: absolute;
  top: 20px;
  right: 30px;
  font-size: 30px;
  cursor: pointer;
  color: #fff;
}

/* Style the close button on hover */
.close-button:hover {
  color: #ccc;
}

/* Add some padding for the body when the lightbox is open */
body.lightbox-open {
  overflow: hidden;
  padding-right: 17px; /* Adjust as needed to prevent page jump */
}

/* Ensure lightbox is centered vertically and horizontally */
.lightbox::before {
  content: "";
  display: inline-block;
  height: 40px;
  margin-right: -0.25em; /* Adjust for centering */
}

